import { dialog } from 'app/components/DialogNotify';
import { updatePurchaseItem } from 'app/modules/Purchase/redux/purchaseItemSlice';
import { createTracking } from 'app/modules/Purchase/redux/trackingSlice';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { FormattedMessage } from 'react-intl';
import { useDispatch } from 'react-redux';
import {
    Form,
    FormGroup,
    Label,
    Modal,
    ModalBody,
    ModalFooter,
    ModalHeader
} from 'reactstrap';

DialogEdit.propTypes = {
    show: PropTypes.bool,
    onHide: PropTypes.func,
    onUpdate: PropTypes.func,
    itemDetail: PropTypes.object,
    onSearchCustomer: PropTypes.func,
    onSuccess: PropTypes.func
};

function DialogEdit({
    show,
    onSearchCustomer = null,
    onHide,
    onUpdate,
    itemDetail,
    onSuccess,
    intl
}) {
    const dispatch = useDispatch();

    const { register, handleSubmit } = useForm();
    const [tracking, setTracking] = useState(null);
    const [code, setCode] = useState('');

    useEffect(() => {
        if (show) {
            setTracking(itemDetail.tracking);
            setCode(itemDetail.tracking?.code || '');
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [show, itemDetail.tracking?.code]);

    const handleTrackingChange = () => {
        if (tracking?.code) {
            // onRemoveTracking();
            handleRemoveTracking();
        } else {
            // onNewTracking(code);
            handleNewTracking();
        }
    };

    const handleUpdateItem = data => {
        setCode('');
        onUpdate(data);
    };

    const handleInputChange = e => {
        const reg = /^[0-9]*$/;
        const check = reg.test(e.target.value);
        if (!check) return;

        setCode(e.target.value);
    };

    const handleNewTracking = () => {
        const params = {
            code
        };
        dispatch(createTracking(params)).then(res => {
            if (res.type.includes('fulfilled')) {
                const params = {
                    id: itemDetail.id,
                    body: {
                        tracking_id: res.payload?.id
                    }
                };
                dispatch(updatePurchaseItem(params)).then(res => {
                    if (res.type.includes('fulfilled')) {
                        dialog.success(
                            intl.formatMessage({
                                id: 'PURCHASE.CREATE.TRACKING.SUCCESS'
                            })
                        );
                        onSuccess();
                        setTracking({
                            ...tracking,
                            code
                        });
                    } else {
                        dialog.error(
                            intl.formatMessage({
                                id: 'PURCHASE.CREATE.TRACKING.FAIL'
                            })
                        );
                    }
                });
            } else {
                dialog.error(
                    intl.formatMessage({ id: 'PURCHASE.CREATE.TRACKING.FAIL' })
                );
            }
        });
    };

    const handleRemoveTracking = () => {
        const params = {
            id: itemDetail.id,
            body: {
                tracking_id: null
            }
        };
        dispatch(updatePurchaseItem(params)).then(res => {
            if (res.type.includes('fulfilled')) {
                dialog.success(
                    intl.formatMessage({
                        id: 'PURCHASE.REMOVE.TRACKING.SUCCESS'
                    })
                );
                onSuccess();
                setTracking(null);
                setCode('');
            } else {
                dialog.error(
                    intl.formatMessage({ id: 'PURCHASE.REMOVE.TRACKING.FAIL' })
                );
            }
        });
    };

    return (
        <Modal isOpen={show} style={{ minWidth: '850px' }}>
            <ModalHeader>
                <FormattedMessage id="PURCHASE.UPDATE.ITEM.TITLE" />
            </ModalHeader>
            <ModalBody>
                <p className="font-size-h6 font-weight-bolder opacity-80">
                    {itemDetail.order_item?.product_id || ''}
                </p>
                {/* begin form */}
                <Form
                    onSubmit={handleSubmit(handleUpdateItem)}
                    id="form-update"
                >
                    <FormGroup className="row">
                        <div className="col-4">
                            <Label>
                                <FormattedMessage id="ORDER.PRICE" />
                            </Label>
                            <input
                                ref={register}
                                name="price"
                                type="number"
                                min="1"
                                className="form-control"
                                defaultValue={itemDetail.price}
                                placeholder={intl.formatMessage({
                                    id: 'GLOBAL.PLACEHOLER.INPUT'
                                })}
                            />
                        </div>
                        <div className="col-4">
                            <Label>
                                <FormattedMessage id="ORDER.QUANTITY" />
                            </Label>
                            <input
                                ref={register}
                                name="quantity"
                                type="number"
                                min="1"
                                className="form-control"
                                defaultValue={itemDetail.quantity}
                                placeholder={intl.formatMessage({
                                    id: 'GLOBAL.PLACEHOLER.INPUT'
                                })}
                            />
                        </div>
                        <div className="col-4">
                            <Label>
                                <FormattedMessage id="ORDER.TRACKING" />
                            </Label>
                            <div className="input-group">
                                <input
                                    type="text"
                                    name="code"
                                    className="form-control"
                                    value={code}
                                    onChange={handleInputChange}
                                    disabled={tracking?.code}
                                    placeholder={intl.formatMessage({
                                        id: 'GLOBAL.TRACKING.PLACEHOLER.INPUT'
                                    })}
                                />
                                <div className="input-group-append">
                                    <button
                                        type="button"
                                        className="input-group-text"
                                        style={{
                                            backgroundColor: '#3699FF',
                                            color: '#ffffff'
                                        }}
                                        onClick={handleTrackingChange}
                                        disabled={!code}
                                    >
                                        {tracking?.code
                                            ? intl.formatMessage({
                                                  id: 'GLOBAL.BUTTON.DELETE'
                                              })
                                            : intl.formatMessage({
                                                  id: 'GLOBAL.BUTTON.SAVE'
                                              })}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </FormGroup>
                </Form>

                <div className="form-group row">
                    <div className="col-12">
                        <Label>
                            <FormattedMessage id="ORDER.PROPERTIES" />
                        </Label>
                        <textarea
                            rows={2}
                            className="form-control"
                            value={itemDetail.order_item?.properties || ''}
                            disabled
                        />
                    </div>
                </div>
                <div className="form-group row">
                    <div className="col-12">
                        <Label>
                            <FormattedMessage id="ORDER.NOTE" />
                        </Label>
                        <textarea
                            rows={2}
                            className="form-control"
                            value={itemDetail.order_item?.note || ''}
                            disabled
                        />
                    </div>
                </div>

                {/* end form */}
            </ModalBody>
            <ModalFooter>
                <button
                    style={{ minWidth: '100px' }}
                    type="button"
                    onClick={onHide}
                    className="btn btn-light btn-elevate"
                >
                    <FormattedMessage id="GLOBAL.BUTTON.CANCEL" />
                </button>
                <button
                    type="submit"
                    form="form-update"
                    className="btn btn-primary btn-elevate"
                >
                    <FormattedMessage id="GLOBAL.BUTTON.UPDATE" />
                </button>
            </ModalFooter>
        </Modal>
    );
}

export default DialogEdit;
