import { Divider } from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { useRef, useState } from 'react';
import { FormattedMessage } from 'react-intl';
import { shallowEqual, useSelector } from 'react-redux';
import { Prompt } from 'react-router-dom';
import Select from 'react-select';
import { Card, CardBody } from '_metronic/_partials/controls';

NewForm.propTypes = {
    suppliers: PropTypes.array,
    isSuccessNew: PropTypes.bool.isRequired,

    onSearchSupplier: PropTypes.func,
    onSubmit: PropTypes.func
};

function NewForm({
    suppliers,
    onSearchSupplier,
    onSubmit,
    btnRef,
    isSuccessNew,
    intl
}) {
    const [supplierSelected, setSupplierSelected] = useState(null);
    const [tax, setTax] = useState(0);
    const [cost, setCost] = useState(0);
    const { taxList } = useSelector(
        ({ home }) => ({ taxList: home.home.taxList }),
        shallowEqual
    );

    // supplier
    const supplierRef = useRef(null);
    const handleSearchSupplier = value => {
        if (supplierRef.current) {
            clearTimeout(supplierRef.current);
        }
        supplierRef.current = setTimeout(() => {
            if (value.length > 0) {
                if (onSearchSupplier) onSearchSupplier(value);
            }
        }, 500);
    };

    const handleSelectSupplier = supplierSelected => {
        setSupplierSelected(supplierSelected);
    };

    const handleSubmit = () => {
        const params = {
            supplier_id: supplierSelected?.value,
            tax,
            cost
        };
        onSubmit(params);
    };

    const handleTaxSelect = tax => {
        setTax(tax.percent);
    };

    const handleInputChange = e => {
        setCost(e.target.value);
    };

    // options
    const supplierOptions = suppliers.map(supplier => {
        return {
            value: supplier.id,
            label: supplier.name,
            email: supplier.email,
            address: supplier.address,
            note: supplier.note
        };
    });

    const taxOptions = taxList?.map(item => {
        return {
            value: item.id,
            label: item.name,
            percent: item.percent
        };
    });

    const formIsHalfFilledOut = isSuccessNew ? false : !!supplierSelected;

    return (
        <>
            <div className="row">
                {/* begin buyer */}
                <div className="col-xl-6 mb-8">
                    <Card className="h-100">
                        <CardBody>
                            <div className="form-group row  align-items-center mt-3">
                                <div className="col-6">
                                    <h4 className=" text-dark font-weight-bold mb-2">
                                        <FormattedMessage id="TRACKING.COST" />
                                    </h4>
                                </div>
                            </div>
                            <Divider className="mb-9" />
                            {/* begin input */}
                            <div className="form-group row">
                                <div className="col-3">
                                    <label>
                                        <FormattedMessage id="ORDER.TAX" />
                                    </label>
                                </div>
                                <div className="col-9">
                                    <Select
                                        options={taxOptions}
                                        defaultValue={taxOptions[0]}
                                        placeholder={intl.formatMessage({
                                            id:
                                                'GLOBAL.SEARCH.PLACEHOLER_SELECT'
                                        })}
                                        onChange={handleTaxSelect}
                                    />
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-3">
                                    <label>
                                        <FormattedMessage id="ORDER.COST" />
                                    </label>
                                </div>
                                <div className="col-9">
                                    <input
                                        value={cost}
                                        className="form-control"
                                        type="number"
                                        onChange={handleInputChange}
                                    />
                                </div>
                            </div>
                            {/* end input */}
                        </CardBody>
                    </Card>
                </div>
                {/* end buyer */}
                {/* begin supplier */}
                <div className="col-xl-6 mb-8">
                    <Card className="h-100">
                        <CardBody>
                            <div className="form-group row  align-items-center">
                                <div className="col-5">
                                    <h4 className=" text-dark font-weight-bold mb-2">
                                        <FormattedMessage id="ORDER.SUPPLIER" />
                                    </h4>
                                </div>
                                <div className="col-7">
                                    <Select
                                        options={supplierOptions}
                                        onInputChange={handleSearchSupplier}
                                        onChange={handleSelectSupplier}
                                        placeholder={intl.formatMessage({
                                            id: 'GLOBAL.SEARCH.PLACEHOLER_INPUT'
                                        })}
                                    />
                                </div>
                            </div>
                            <Divider className="mb-9" />
                            <div className="form-group row">
                                <div className="col-3">
                                    <label>
                                        <FormattedMessage id="ORDER.NAME" />
                                    </label>
                                </div>
                                <div className="col-9">
                                    <div className="form-control bg-light">
                                        {supplierSelected?.label}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-3">
                                    <label>
                                        <FormattedMessage id="ORDER.ADDRESS" />
                                    </label>
                                </div>
                                <div className="col-9">
                                    <div className="form-control bg-light">
                                        {supplierSelected?.address}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-3">
                                    <label>
                                        <FormattedMessage id="AUTH.INPUT.EMAIL" />
                                    </label>
                                </div>
                                <div className="col-9">
                                    <div className="form-control bg-light">
                                        {supplierSelected?.email}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-3">
                                    <label>
                                        <FormattedMessage id="ORDER.NOTE" />
                                    </label>
                                </div>
                                <div className="col-9">
                                    <div className="form-control bg-light">
                                        {supplierSelected?.note}
                                    </div>
                                </div>
                            </div>
                        </CardBody>
                    </Card>
                </div>
                {/* end supplier */}
                <button
                    className="d-none"
                    onClick={handleSubmit}
                    ref={btnRef}
                />
            </div>
            <Prompt
                when={formIsHalfFilledOut}
                message={intl.formatMessage({
                    id: 'GLOBAL.MESSAGE.FILLED_OUT'
                })}
            />
        </>
    );
}

export default NewForm;
