import ItemTable from './ItemTable';
import formatNumber from 'helper/formatNumber';
import PropTypes from 'prop-types';
import React from 'react';
import { Card, CardBody, CardHeader } from '_metronic/_partials/controls';
import { FormattedMessage } from 'react-intl';

ItemCard.prototype = {
    items: PropTypes.array.array
};

function ItemCard({ items, intl }) {
    const columns = [
        { id: 'id', title: intl.formatMessage({ id: 'TRACKING.ID' }) },
        {
            id: 'product',
            title: intl.formatMessage({ id: 'TRACKING.PRODUCT' })
        },
        { id: 'price', title: intl.formatMessage({ id: 'TRACKING.PRICE' }) },
        {
            id: 'quantity',
            title: intl.formatMessage({ id: 'TRACKING.QUANTITY' })
        },
        { id: 'box', title: intl.formatMessage({ id: 'TRACKING.FORMALITY' }) },
        {
            id: 'property',
            title: intl.formatMessage({ id: 'TRACKING.PROPERTY' })
        },
        { id: 'cost', title: intl.formatMessage({ id: 'TRACKING.COST' }) },
        { id: 'note', title: intl.formatMessage({ id: 'TRACKING.NOTE' }) }
    ];

    const rows = items.map(item => {
        return {
            id: item.id || '',
            product: item.product_id || '',
            price: formatNumber(item.price) || 0,
            quantity: formatNumber(item.quantity) || 0,
            box: item.is_box
                ? intl.formatMessage({ id: 'TRACKING.PIECE' })
                : intl.formatMessage({ id: 'TRACKING.BOX' }),
            property: item.properties ? item.properties : '-',
            cost: (
                <div className="item-card">
                    <p>
                        <FormattedMessage id="TRACKING.MONEY_GOODS" />:{' '}
                        {formatNumber(item.amount)}
                    </p>
                    <p>
                        <FormattedMessage id="TRACKING.TAX_PERCENT" />:{' '}
                        {item.tax_percent}%
                    </p>
                    <p>
                        <FormattedMessage id="TRACKING.TAX" />:{' '}
                        {formatNumber(item.tax)}
                    </p>
                    <p>
                        <b>
                            <FormattedMessage id="TRACKING.BALANCE" />:
                        </b>{' '}
                        {formatNumber(item.balance)}
                    </p>
                </div>
            ),
            note: item.note || '-'
        };
    });

    return (
        <Card>
            <CardHeader
                title={intl.formatMessage({ id: 'TRACKING.DETAIL.ITEM.TITLE' })}
            />
            <div>
                <CardBody>
                    <ItemTable
                        columns={columns}
                        rows={rows}
                        isAction={false}
                        page={1}
                        lastpage={1}
                    />
                </CardBody>
            </div>
        </Card>
    );
}

export default ItemCard;
