import { checkNumber } from 'helper/utils';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { ModalBody } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { FormattedMessage } from 'react-intl';
import { FormGroup, Label, Modal, ModalFooter, ModalHeader } from 'reactstrap';
import './index.scss';

DialogNew.propTypes = {
    onHide: PropTypes.func,
    onNew: PropTypes.func,

    show: PropTypes.bool
};

function DialogNew({ show = false, onHide, onNew }) {
    const { register, handleSubmit } = useForm();
    const [code, setCode] = useState('');

    const handleInputChange = e => {
        const check = checkNumber(e.target.value);
        if (!check) return;
        setCode(e.target.value);
    };

    const handleNew = ({ code }) => {
        const params = {
            code
        };
        onNew(params);
    };

    return (
        <Modal isOpen={show} className="container-modal">
            <ModalHeader>
                <FormattedMessage id="TRACKING.CREATE.TITLE" />
            </ModalHeader>
            <ModalBody>
                <form id="form-new" onSubmit={handleSubmit(handleNew)}>
                    <FormGroup>
                        <Label>
                            <FormattedMessage id="TRACKING.CODE_TRACKING" />
                        </Label>
                        <input
                            ref={register}
                            className="form-control"
                            name="code"
                            value={code}
                            onChange={handleInputChange}
                        />
                    </FormGroup>
                </form>
            </ModalBody>
            <ModalFooter>
                <button
                    type="button"
                    onClick={onHide}
                    className="btn btn-light btn-elevate"
                >
                    <FormattedMessage id="GLOBAL.BUTTON.CANCEL" />
                </button>
                <> </>
                <button
                    type="submit"
                    className="btn btn-primary btn-elevate"
                    form="form-new"
                    disabled={!code}
                >
                    <FormattedMessage id="GLOBAL.BUTTON.CREATE" />
                </button>
            </ModalFooter>
        </Modal>
    );
}

export default DialogNew;
